import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import StaffSiteActivityCardMenu from "./StaffSiteActivityCardMenu";
import { Link, useLocation, useParams } from "react-router-dom";
const StaffSiteActivityCard = ({ pso, onRefetch }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [open, setOpen] = useState(false);
  const onToggle = (e) => {
    if (e) {
      e.preventDefault();
    }
    setOpen((pre) => !pre);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member?a=activityproject&type=activity-detail`}
        state={{
          ...getQuery.state,
          activity: pso,
        }}
      >
        <div
          className={style.smc_card}
          style={{
            alignItems: "flex-start",
          }}
        >
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner}>
              <div className={style.smc_card_contatiner_inner}>
                <h6>
                  {t("activity_name")} {": "}
                  {pso?.activity_name ?? ""}
                </h6>
                <p>
                  {t("activity_type")} {": "}
                  {pso?.activity_type ?? ""}
                </p>
                <p>
                  {t("universal_batch")} {": "}
                  {pso?.activity_batch?.batchName ?? ""}
                </p>
              </div>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onToggle}
          />
        </div>
      </Link>

      {open && (
        <StaffSiteActivityCardMenu
          onClose={onToggle}
          menuData={pso}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default StaffSiteActivityCard;
