import style from "../PlateformUser/PlateformUser.module.css";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../Utils/Functions/nextPage";
import { customDebounce } from "../../Utils/Functions/debounce";
import { useInstituteAllStudent } from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import InstituteStudentCard from "./InstituteStudentCard";
import QvipleLoader from "../../Loader/QvipleLoader";
import Loading from "../../Loader/Loading";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../Ui/Button/CreateButton";
const InstituteStudent = ({
  title,
  onClose,
  selectedId,
  onSelectStudent,
  instituteId,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
  buttonTitle,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { instituteAllStudent, instituteAllStudentLoading } =
    useInstituteAllStudent({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteAllStudent?.list?.length) {
      let obj = nextPage(instituteAllStudent?.list?.length);
      setState(() => obj);
    }
  }, [instituteAllStudent?.list]);

  const onSearchEvent = (e) => {
    if (!instituteAllStudentLoading && instituteAllStudent?.list?.length >= 0) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStudent(value);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{title ?? t("student_list")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isMultiSelect && (
                <CreateButton
                  label={t(buttonTitle ?? "select")}
                  count={selectList?.length}
                  onAction={onDone}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {instituteAllStudent?.list?.map((student, index) =>
            instituteAllStudent?.list?.length === index + 1 ? (
              <div key={student?._id} ref={ref}>
                <InstituteStudentCard
                  student={student}
                  selectedId={selectedId}
                  onSelect={onSelect}
                  selectList={selectList}
                  isMultiSelect={isMultiSelect}
                  onMultiSelect={onMultiSelect}
                />
              </div>
            ) : (
              <InstituteStudentCard
                student={student}
                key={student?._id}
                selectedId={selectedId}
                onSelect={onSelect}
                selectList={selectList}
                isMultiSelect={isMultiSelect}
                onMultiSelect={onMultiSelect}
              />
            )
          )}
          {instituteAllStudentLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            instituteAllStudent?.list?.length === 0 && (
              <EmptyMessage title="No students found related this search." />
            )
          ) : (
            instituteAllStudent?.list?.length === 0 && (
              <EmptyMessage title="No students." />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default InstituteStudent;
