import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import ActivityNotice from "./ActivityNotice/ActivityNotice";
import ActivityPermissionLetter from "./ActivityPermissionLetter/ActivityPermissionLetter";
import ActivityReports from "./ActivityReports/ActivityReports";
import ActivityAttendance from "./ActivityAttendance/ActivityAttendance";
import ActivityOther from "./ActivityOther/ActivityOther";
import { useTranslation } from "react-i18next";
import { useCallback } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const StaffSiteActivityDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();

  const [searchParams] = useSearchParams();
  const onNavigate = useCallback(() => {
    let url = "";
    url = `/q/${params.username}/member?a=activityproject`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>
              {getQuery.state?.activity?.activity_name ?? ""} ({t("activity")})
            </h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={
                tab.id === "notice" && !searchParams.get("n")
                  ? true
                  : searchParams.get("n") === tab.id
                  ? true
                  : false
              }
              parentState={getQuery.state}
              url={`/q/${params.username}/member?a=activityproject&type=activity-detail${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>

        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
      </UserMemberContentWrapper>
      {!searchParams.get("n") ? (
        <ActivityNotice acid={getQuery.state?.activity?._id} />
      ) : searchParams.get("n") === "letter" ? (
        <ActivityPermissionLetter acid={getQuery.state?.activity?._id} />
      ) : searchParams.get("n") === "reports" ? (
        <ActivityReports acid={getQuery.state?.activity?._id} />
      ) : searchParams.get("n") === "attendance" ? (
        <ActivityAttendance acid={getQuery.state?.activity?._id} />
      ) : searchParams.get("n") === "other" ? (
        <ActivityOther acid={getQuery.state?.activity?._id} />
      ) : null}
    </>
  );
};

export default StaffSiteActivityDetail;

const tabList = [
  {
    label: "notice",
    id: "notice",
    urlKey: "",
  },
  {
    label: "permission_letter",
    id: "letter",
    urlKey: "&n=letter",
  },
  {
    label: "activity_reports",
    id: "reports",
    urlKey: "&n=reports",
  },
  {
    label: "attendance",
    id: "attendance",
    urlKey: "&n=attendance",
  },
  {
    label: "other",
    id: "other",
    urlKey: "&n=other",
  },
];
